.input {
  display: block;
  position: relative;
}
.input__input,
.input__textarea {
  display: block;
  width: 100%;
  border: 0;
  outline: 0;
  border-bottom: 3px solid #1caf5e;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 500;
  padding: 20px 0px 8px;
  margin-bottom: 30px;
  transition: all 0.1s ease-out;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.input__input:focus,
.input__textarea:focus,
.input__input:valid,
.input__textarea:valid,
.input__input:disabled,
.input__textarea:disabled {
  border-bottom: 3px solid #d5d5d5;
}
.input__input:focus ~ .input__label,
.input__textarea:focus ~ .input__label,
.input__input:valid ~ .input__label,
.input__textarea:valid ~ .input__label,
.input__input:disabled ~ .input__label,
.input__textarea:disabled ~ .input__label {
  top: 6px;
  font-size: 11px;
  color: #1caf5e;
}
.input__textarea {
  resize: none;
  max-width: 100%;
  min-width: 100%;
}
.input__label {
  display: block;
  position: absolute;
  color: #898989;
  font-size: 15px;
  font-weight: 500;
  left: 0px;
  top: 22px;
  pointer-events: none;
  transition: all 0.1s ease-out;
}
.input__error {
  position: absolute;
  top: 100%;
  left: 0;
  font-size: 13px;
  font-weight: 300;
  color: #e94c36;
}
.input--error .input__input,
.input--error .selectize-input {
  border: 1px solid #e94c36;
  color: #e94c36;
}
.input--error .input__label {
  color: #e94c36;
}
